.categories-footer {
  padding: 0.625rem;
  background-color: #fff;
  margin-top: 0.46875rem;
  margin-bottom: 0.46875rem;
  padding-bottom: 0.3125rem;
}
.categories-footer .menu {
  padding-top: 0;
  clear: both;
}
.categories-footer .menu .item {
  padding: 0 0 0 0.9375rem;
  margin: 0 0 0.3125rem 0;
  line-height: 2.03125rem;
  background-color: #FFF5E2;
  overflow: hidden;
}
.categories-footer .menu .item a {
  color: #333;
  display: block;
}
.categories-footer .menu .item .text {
  display: inline-block;
  vertical-align: middle;
}
.categories-footer .menu .item .arrow-tx-right {
  background-image: url('https://img.limeetpet.com/limeet/arrow-right.png');
  background-repeat: no-repeat;
  background-size: 120%;
  width: 0.75rem;
  height: 0.75rem;
  vertical-align: middle;
  float: right;
  margin-right: 0.625rem;
  margin-top: 0.625rem;
}
.categories-footer .menu .item:hover {
  background-color: #F6912D;
  color: #fff;
}
.categories-footer .menu .item:hover a:hover {
  color: #fff;
}
.categories-footer .menu .item:hover .arrow-tx-right {
  background-image: url('https://img.limeetpet.com/limeet/arrow-right2.png');
}
.categories-footer .describe {
  margin: 0 auto;
  padding: 0.625rem;
}
.categories-footer .describe p {
  padding: 0.3125rem 0.625rem;
}
.categories-footer .describe p img {
  display: inline-block;
  width: 1.25rem;
  padding-right: 0.15625rem;
}
.categories-footer .describe a {
  color: #F6912D;
  padding-left: 0.625rem;
  display: inline-block;
}
